@import "scss/common/mixins";

.banner {
  background-image: url("/img/background/bg_banner.png");
  background-size: cover;
  background-position: center;
  @include width-height-value(100%, 100%);
  background-color: #15141a;
}

.banner-content {
  @include flex-container(row, space-between, flex-start);
  flex-wrap: nowrap;

  .device-banner {
    width: 515px;
    height: 519px;
  }

  .banner-content-text {
    @include flex-container(column, center, flex-start);

    .main-sub-title {

      font-size: 14px;
      line-height: 30px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.1em;
      color: #008ed6;
    }

    .main-title {
      font-size: 48px;
      line-height: 58px;
      font-weight: 600;
      text-transform: none;
      letter-spacing: normal;
      color: #ffffff;
    }

    .main-text {
      font-size: 18px;
      font-weight: 400;
      text-transform: none;
      letter-spacing: normal;
      color: #999999;
    }

    .block-button {
      @include flex-container(row, space-between, center);

      .button-down {
        width: 160px;
        height: 52px;
        background-color: #008ed6;
        box-shadow: 0 0 3px 0 rgba(3, 3, 3, 0.1);
        font-size: 14px;
        font-weight: 700;
        color: #ffffff;
        margin-right: 15px;
      }

      .button-view {
        width: 160px;
        height: 52px;
        background-color: rgba(0, 142, 214, 0.0);
        border: 1px solid rgba(255, 255, 255, 0.2);
        font-size: 14px;
        font-weight: 700;
        color: #ffffff;
      }
    }
  }
}

@import "banner-media";